<template>
  <v-card>
    <v-form>
      <v-stepper v-model="e1"
                 :light='true'>
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1"
                          step="1">{{$t('label_Copy_from_template')}}</v-stepper-step>
          <v-divider></v-divider>

          <v-stepper-step :complete="e1 > 2"
                          step="2">{{$t('label_Customer_data')}}</v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step :complete="e1 > 3"
                          step="3">{{$t('label_Functional_authority')}}</v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step step="4"
                          :complete="e1 > 4">{{$t('label_Email_Whitelist')}}</v-stepper-step>
          <v-divider></v-divider>

          <v-stepper-step step="5">{{$t('label_language')}}</v-stepper-step>

        </v-stepper-header>

        <v-stepper-items>
          <!-- 从现有模板复制 -->
          <v-stepper-content step="1">
            <v-card class="pl-6 pr-6">
              <v-form ref="copyform">
                <v-row>
                  <v-col cols="6">
                    <mrc_ess_prompt :label="'Client Source' + '*'"
                                    v-model="client"
                                    :obj='clientSource'>
                    </mrc_ess_prompt>
                  </v-col>
                  <v-col cols="6">
                    <mrc_ess_prompt :label="'Company'+ '*'"
                                    :obj='companyName'
                                    v-model="companyuuid"
                                    @getData='getclientcontainers'>
                    </mrc_ess_prompt>
                  </v-col>
                </v-row>
              </v-form>
              <div class="contentbox">
                <v-card v-for="(item,index) in Object.keys(clientcontainers)"
                        :key="index"
                        class="bg2">
                  <clientcontainers :containerName='item'
                                    :list='clientcontainers[item]'
                                    ref="childrenClient_containers"></clientcontainers>
                </v-card>
              </div>
            </v-card>
            <mrc_ess_submitBtn @cancel='edmit'
                               @success='e1=2'
                               :cancelText="$t('label_cancel')"
                               :successText="$t('label_The_next_step')"
                               :disabled='btndisabled'></mrc_ess_submitBtn>
          </v-stepper-content>

          <!-- 客户资料 -->
          <v-stepper-content step="2"
                             class='bg1'>
            <v-card>
              <adduser ref="zjadduser"></adduser>
            </v-card>
            <mrc_ess_submitBtn @cancel='e1=1'
                               @success='getuserzl()'
                               :cancelText="$t('label_The_previous_step')"
                               :successText="$t('label_The_next_step')"
                               :disabled='btndisabled'></mrc_ess_submitBtn>
          </v-stepper-content>
          <!-- 功能权限 -->
          <v-stepper-content step="3"
                             class='bg1'>
            <v-card>
              <quanxian ref="zjquanxian"
                        v-if="e1>=3"
                        :uuidslist='copyuuidslist'></quanxian>
            </v-card>
            <mrc_ess_submitBtn @cancel='e1 = 2'
                               @success='getuserqx'
                               :cancelText="$t('label_The_previous_step')"
                               :successText="$t('label_The_next_step')"></mrc_ess_submitBtn>
          </v-stepper-content>
          <!-- 邮箱白名单 -->
          <v-stepper-content step="4"
                             class='bg1'>
            <v-card>
              <baimingdan ref='zjbaimingdan'></baimingdan>
            </v-card>

            <mrc_ess_submitBtn @cancel='e1 = 3'
                               @success='getuseryx'
                               :cancelText="$t('label_The_previous_step')"
                               :successText="$t('label_The_next_step')"></mrc_ess_submitBtn>
          </v-stepper-content>
          <!-- 语言 -->
          <v-stepper-content step="5"
                             class='bg1'>
            <v-card>
              <yuyan ref="reflanguage"></yuyan>
            </v-card>

            <mrc_ess_submitBtn @cancel='e1 = 4'
                               @success='submit()'
                               :loading='btnloading'
                               :cancelText="$t('label_The_previous_step')"></mrc_ess_submitBtn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-form>
  </v-card>
</template>

<style lang="scss" scoped>
.bg1 {
  background-color: #eeeef0;
}
.btn {
  color: rgb(255, 173, 77);
  margin: 27px 60px;
  border-radius: 20%;
}
.btn:hover {
  background-color: rgb(255, 173, 77);
  color: white;
}
.btnbgd {
  background-color: #eeeef0;
  height: 80px;
}
.cardbox {
  background-color: white;
  padding: 0 30px;
}
.contentbox {
  background-color: white;
  padding: 10px 20px;
}
</style>

<script>
import clientcontainers from './clientdetail/modules/clientcontainers'
import adduser from './clientdetail/modules/adduser'
import baimingdan from './clientdetail/baimingdan'
import quanxian from './clientdetail/quanxianpeizhi'
import yuyan from './clientdetail/yuyan'
import { copyadduserclientdetail, copyuserclientdetail } from '@/service/api/clientmanage_api.js'
import { getBusinessList } from '@/service/api/func_module.js'
export default {
  components: {
    adduser, baimingdan, quanxian, yuyan, clientcontainers
  },
  data () {
    return {
      btnloading: false,
      btndisabled: true,
      rules: [value => !!value || '不能为空.'],
      valid: false,
      e1: 1,
      client: '',
      companyuuid: '',
      clientSource: { prompt_name: 'mrbase_sys_clients' },
      Children: { //容器
      },
      copyuuidslist: [], //用户默认选中的权限列表
      function_permission: [],
      client_languages: [],
      select1: '',  //预设语言
      select2: [],  //其他语言
      listitems: [],   //下拉选择list
      ysdefault: {},   //预设默认选择  判断用户是否修改
      otherlanguagelist: [],  //  保存用户修改前的其他语言
      mrbase_sys_function_permission: []//copy选择权限列表
    }
  },
  computed: {
    clientcontainers () {
      const dd = {}
      for (var key in this.Children) {
        if (Array.isArray(this.Children[key])) {
          // console.log(key, this.Children[key]);
          dd[key] = this.Children[key]
        }
      }
      return dd
    },
    companyName () {
      return { prompt_name: 'org_level_relevance_client', client_uuid: this.client }
    },
    listClient_containers () {   //容器资料
      var client_containersarr = []
      if (this.$refs.childrenClient_containers > 0) {
        this.$refs.childrenClient_containers.map(item => {
          client_containersarr = client_containersarr.concat(JSON.parse(JSON.stringify(item.copylist)))
        })
      }
      const list = []
      if (client_containersarr.length > 0) {
        client_containersarr.map(item => {
          if (item.checked == true) {
            delete item.checked
            list.push(item)
          }
        })
      }
      return list
    },
    xzuserlist () {  //新增用户资料
      if (!this.$refs.zjadduser.userdetail.client_address_province_id) {
        delete this.$refs.zjadduser.userdetail.client_address_province_id
      }
      if (!this.$refs.zjadduser.userdetail.client_address_city_id) {
        delete this.$refs.zjadduser.userdetail.client_address_city_id
      }
      // console.log('未选择城市', this.$refs.zjadduser.userdetail);
      return this.$refs.zjadduser.userdetail
    },
    // mrbase_sys_function_permission () {     //客户功能角色权限子表
    //   console.log(this.$refs.zjquanxian.getchangeref());
    //   return this.$refs.zjquanxian.getchangeref()
    // },
    mrbase_sys_client_contacts () {     //客户联系人信息表
      return this.$refs.zjadduser.desserts //数组
    },
    // mrbase_sys_client_email_whitelist
    mrbase_sys_client_email_whitelist () {
      const baimingdan = []     //客户邮箱白名单子表
      this.$refs.zjbaimingdan.states.forEach(item => {
        const obj = {
          "~table~": "mrbase_sys_client_email_whitelist",
          "whitelist_domain": item
        }
        baimingdan.push(obj)
      })  //数组
      return baimingdan
    }
  },
  async mounted () {
    const res = await getBusinessList('client_language')
    // console.log(res.data);
    if (res.data.length > 0) {
      res.data.map(item => {  //下拉列表
        this.listitems.push(item.view_fields)
      })
    }

  },
  methods: {
    changee1 (v) {
      this.e1 = v
    },
    async getclientcontainers () {  //后台容器数据     //copy_client_uuid 变为client_uuid  copy_org_level_uuid:org_level_uuid
      const res = await copyuserclientdetail({ client_uuid: this.client, org_level_uuid: this.companyuuid })
      if (res.code == 200) {
        this.Children = res.data.client_containers
        this.copyuuidslist = res.data.mrbase_sys_function_permission  //用户默认选中权限列表 
        this.function_permission = res.data.function_permission_tree   ////功能权限列表
        this.btndisabled = false
        // this.client_languages = res.data.client_languages
        // console.log(this.client_languages);
        //生成语言列表
        // this.getlanguage(this.client_languages)
      }
    },
    // saveClientcontainers () {  //获取容器数据
    //   // console.log(this.listClient_containers)
    // },
    getuserzl () {  //客户资料获取
      // this.e1 = 3
      if (this.$refs.zjadduser.formverify()) {  //表单验证
        this.e1 = 3
      }
      else {
        this.$myConfirm({ content: this.$t('message_The_information_was_not_entered_completely') }).then(() => { }).catch(() => { })
      }
    },
    // 获取权限数据
    getuserqx (val) {
      if (!this.$refs.zjquanxian.getchangeref().length > 0) {
        this.$myConfirm({
          content: this.$t('message_permission_list_must_select_at_least_one_item')
        })
      }
      else {
        this.e1 = 4
        this.mrbase_sys_function_permission = this.$refs.zjquanxian.getchangeref()
      }
    },
    // 获取邮箱白名单
    getuseryx () {
      if (this.$refs.zjbaimingdan.isemail != '') {
        this.$myConfirm({
          content: this.$t('verify_The_mailbox_format_is_wrong')
        })
      }
      else {
        this.e1 = 5
      }
    },
    getlanguage (val) {
      if (val.length > 0) {
        val.map((item, index) => {  //获取数据
          if (item.is_default_language == 1) {  //应该等于1
            this.select1 = val[index].client_language_code  //数据要处理
            this.ysdefault = val[index]
          }
          else {
            this.select2.push(val[index])
            this.otherlanguagelist.push(val[index])
          }
        })
      }
    },
    changeis (item) {
      // item.is_default_language = 1
    },
    changeno (item) {
      // item.is_default_language = 0
    },
    // 取消
    edmit () {
      this.$myConfirm({
        content: this.$t('message_Confirm_the_cancellation_of_the_edit')
      }).then(res => {
        this.$router.push({
          name: 'clientmanage'
        })
      }).catch(err => { })
    },
    // 总提交 添加新的用户   
    submit () {
      if (this.$refs.reflanguage.mrbase_sys_client_language.length) {
        let file1 = this.$refs.zjadduser.light_mode_icon_file
        let file2 = this.$refs.zjadduser.dark_mode_icon_file
        if (file1 == '') {
          this.xzuserlist.light_mode_icon = {
            name: '',
            path: '',
            size: ''
          }
        }
        if (file2 == '') {
          this.xzuserlist.dark_mode_icon = {
            name: '',
            path: '',
            size: ''
          }
        }
        this.xzuserlist.mrbase_sys_function_permission = this.mrbase_sys_function_permission
        this.xzuserlist.mrbase_sys_client_contacts = this.mrbase_sys_client_contacts || []
        this.xzuserlist.mrbase_sys_client_email_whitelist = this.mrbase_sys_client_email_whitelist || []
        this.xzuserlist.mrbase_sys_client_language = this.$refs.reflanguage.mrbase_sys_client_language
        this.xzuserlist.client_containers = this.listClient_containers || []
        this.xzuserlist.copy_client_uuid = this.client
        this.xzuserlist.copy_org_level_uuid = this.companyuuid
        this.btnloading = !this.btnloading
        copyadduserclientdetail(this.xzuserlist).then(async res => {
          if (res.code === '200') {
            //上传文件
            let res1 = null
            let res2 = null
            if (file1 && file1.length > 0) {
              res1 = await this.$uploadFiles(file1, {
                function_uuid: this.$store.state.menuFunctionUuid,
                attachment_data_table: 'mrbase_sys_clients',
                attachment_data_field: 'light_mode_icon',
                org_level_uuid: this.$store.state.currentCompany,
                uuid: res.data.uuid,
              })
            }
            if (file2 && file2.length > 0) {
              res2 = await this.$uploadFiles(file2, {
                function_uuid: this.$store.state.menuFunctionUuid,
                attachment_data_table: 'mrbase_sys_clients',
                attachment_data_field: 'dark_mode_icon',
                org_level_uuid: this.$store.state.currentCompany,
                uuid: res.data.uuid,
              })
            }
            this.$Message({ text: this.$t('message_The_operation_was_successful') })
            this.$router.push({
              name: 'clientmanage'
            })
          } else {
            this.$Message({ text: res.msg })
            this.btnloading = !this.btnloading
          }
        }).catch(err => {
          this.btnloading = !this.btnloading
        })
      }
      else {
        this.$myConfirm({
          content: this.$t('message_default_language_must_be_selected')
        })
      }
    }
  }
  ,
  watch: {

  }
}
</script>
<style scoped>
.v-stepper {
  box-shadow: none;
}
.v-stepper__header {
  background-color: #eeeef0;
  box-shadow: none;
}
.v-stepper__content {
  padding: 0px 0px;
}
</style>
