<template>
  <div class="pl-8 pr-8 pt-8">
    <!--白名单 -->
    <v-combobox v-model="states"
                :label="$t('label_Email_whitelist_Settings')+'*'"
                multiple
                :items="list"
                :validate-on-blur='true'
                item-text="client_whitelist_email"
                item-value="uuid"
                chips
                @update:search-input='verifyemail'
                @input="emailverify"
                :error-messages='isemail'
                :readonly='readonly'></v-combobox>
  </div>
</template>  

<script> 
export default {
  props: {
    list: {
      type: Array,
      default: () => { return [] }
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      states: [],//默认显示
      disabled: false,
      select: [],  //添加的数组
      datastorage: this.list, //保存已有数据
      isemail: '' //错误消息文本
    }
  },
  watch: {
    list: {
      handler (val) {
        this.states = val
      },
      deep: true
    }
  },
  computed: {
  },
  mounted () {
    if (this.$store.state.detailuuid) {
      this.states = this.list
    }
    else {

    }
  },
  methods: {
    // cancel () { },
    // success () {
    //   if (this.isemail != '') {
    //     console.log('错误');
    //   }
    //   else {
    //     console.log('正确');
    //     console.log(this.states);
    //   }
    // },
    verifyemail (v) {
      if (v != null) {
        const isErr = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(v)
        if (isErr) {
          this.isemail = ''
        }
        else {
          this.isemail = this.$t('verify_The_mailbox_format_is_wrong')
        }
      }
      else {
        this.isemail = ''
      }
    },
    emailverify (v) {
      if (this.isemail != '') {
        this.states.pop()
      }
    }
  }

}
</script>