<template>
  <div class="containersbox">
    <div class="content">
      <!--   mdi-bookmark-outline-->
      <!-- <i class="material-icons"></i> -->
      <v-icon @click="toggleChildren"
              class="material-icons"
              style="color:#0f46c1"
              :style="{ display:!showChildren&&!halfSelected ? 'block' : 'none'}">check_box_outline_blank</v-icon>
      <v-icon @click="toggleChildren"
              class="material-icons"
              style="color:#0f46c1"
              :style="{ display:showChildren&&!halfSelected ? 'block' : 'none'}">check_box</v-icon>
      <v-icon @click="toggleChildren"
              class="material-icons"
              style="color:#0f46c1"
              :style="{ display:halfSelected ? 'block' : 'none'}">indeterminate_check_box</v-icon>
      <div>
        {{containerName}}
      </div>
    </div>

    <div v-for="(item1,index2) in copylist"
         :key="index2"
         class="content margincontent">
      <v-icon @click.stop="changeicon(index2)"
              class="material-icons"
              style="color:#0f46c1">{{!item1.checked ?"check_box_outline_blank":'check_box'}}</v-icon>
      <div>
        {{item1.table_name}}
      </div>
    </div>
  </div>
</template>

<style scoped>
.content {
  display: flex;
}
.margincontent {
  padding-left: 50px;
}
.containersbox {
  margin: 20px 0;
}
</style>

<script>
export default {
  components: {},
  props: {
    containerName: {
      type: String
    },
    list: {
      type: Array
    }
  },
  data () {
    return {
      showChildren: false,
      copylist: [],
      halfSelected: false //办选状态
    };
  },
  created () { },
  mounted () {
    this.copylist = this.list.map(item => {
      item.checked = false
      return item
    })
  },
  methods: {
    toggleChildren () { //全选 
      this.copylist = this.list.map((item, i) => {
        item.checked = !this.showChildren
        return item
      })
      this.showChildren = !this.showChildren
    },
    changeicon (index) {
      this.copylist = this.list.map((item, i) => {
        if (index == i) {
          item.checked = !item.checked
        }
        return item
      })
      // 循环查看是否全选
      const allChecked = this.copylist.every(item => {
        return item.checked == true
      })

      // 循环查看是否全选
      const someChecked = this.copylist.some(item => {
        return item.checked == false
      })
      // 如果 全部选中  
      if (allChecked) {
        this.halfSelected = false
        this.showChildren = !this.showChildren
      }
      //选中一个
      if (someChecked) {
        this.halfSelected = true
        this.showChildren = false
      }
    }
  },
  computed: {

  },
  watch: {
    // 因为名字和list是一起传过来的就监听名字就行了，就不深度监听数组了
    containerName () {
      this.copylist = this.list.map(item => {
        item.checked = false
        return item
      })
    }
  },
};
</script>

<style scoped>
</style>
